/* Actions */
.tangible-logic-modal-actions-box {
  padding: 15px;
  border: 1px solid #dcdcdc; }

.tangible-logic-modal-actions {
  margin-top: 10px; }

.tangible-logic-button,
.tangible-logic-action-icon {
  display: inline-block;
  border: 2px solid transparent !important;
  /* Ensure overriding theme button style */
  margin: 0 8px 0 0;
  padding-left: 10px;
  padding-right: 10px;
  padding-top: 0px;
  padding-bottom: 0px;
  border-radius: 3px;
  height: 33px;
  line-height: 1;
  color: #676f7a !important;
  background-color: #e4e7ea;
  text-decoration: none;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  transition: background-color 0.2s;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif; }

.tangible-logic-button:hover,
.tangible-logic-action-icon:hover {
  background-color: #dadfe5; }

.tangible-logic-button:focus,
.tangible-logic-action-icon:focus {
  border-color: #00a0d2;
  outline: none; }

.tangible-logic-button:last-child,
.tangible-logic-action-icon {
  margin-right: 0; }

.tangible-logic-button[data-tangible-logic='save'] {
  background-color: #00a0d2;
  color: #ffffff !important; }

.tangible-logic-button[data-tangible-logic='save']:hover {
  background-color: #0197c6; }

.tangible-logic-button[data-tangible-logic='save']:focus {
  border-color: #ffc217 !important; }

.tangible-logic-button[data-tangible-logic='save'],
.tangible-logic-button[data-tangible-logic='close'] {
  height: 38px;
  padding-left: 14px;
  padding-right: 14px; }

.tangible-logic-button,
.tangible-logic-action-icon {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none; }
